"use client";

import { useState } from "react";
import Image from "next/image";
import { zodResolver } from "@hookform/resolvers/zod";
import { useQueryClient } from "@tanstack/react-query";
import { useTranslations } from "next-intl";
import { useForm } from "react-hook-form";
import { z } from "zod/v4";

import { Button } from "@trading-platform/ui/button";
import { CircularProgress } from "@trading-platform/ui/circular-progress";
import { Input } from "@trading-platform/ui/input";
import { Label } from "@trading-platform/ui/label";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@trading-platform/ui/select";
import { Textarea } from "@trading-platform/ui/textarea";
import { toast } from "@trading-platform/ui/toast";

import { ASSETS } from "./constants";
import {
  useWaitlistStats,
  WAITLIST_STATS_QUERY_KEY,
} from "./use-waitlist-stats";

const reservationSchema = z.object({
  restaurantName: z.string().min(1, "Restaurant name is required"),
  numberOfLocations: z.string().min(1, "Number of locations is required"),
  yourName: z.string().min(1, "Your name is required"),
  email: z.string().email("Invalid email address"),
  phone: z.string().optional(),
  biggestChallenge: z.string().optional(),
});

type ReservationFormData = z.infer<typeof reservationSchema>;

export function ReservationDialogContent() {
  const t = useTranslations("reservationDialog");
  const { stats } = useWaitlistStats();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const queryClient = useQueryClient();

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
    watch,
    reset,
  } = useForm<ReservationFormData>({
    // @ts-expect-error - zodResolver type compatibility issue with zod v4
    resolver: zodResolver(reservationSchema),
    defaultValues: {
      restaurantName: "",
      numberOfLocations: "",
      yourName: "",
      email: "",
      phone: "",
      biggestChallenge: "",
    },
  });

  const numberOfLocations = watch("numberOfLocations");
  const numberOfLocationsValue =
    numberOfLocations.length > 0 ? numberOfLocations : undefined;

  const onSubmit = async (data: ReservationFormData) => {
    setIsSubmitting(true);
    try {
      const numberOfLocationsNumeric = (() => {
        const digitsRegex = /\d+/;
        const match = digitsRegex.exec(data.numberOfLocations);
        return match ? Number.parseInt(match[0], 10) : null;
      })();

      if (numberOfLocationsNumeric === null) {
        throw new Error(t("invalidNumLocations"));
      }

      const response = await fetch("/api/waitlist", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email: data.email,
          full_name: data.yourName,
          phone: data.phone?.trim() ?? null,
          restaurant_name: data.restaurantName,
          number_of_locations: numberOfLocationsNumeric,
          biggest_challenge: data.biggestChallenge?.trim() ?? null,
        }),
      });

      const result = (await response.json().catch(() => null)) as {
        success: boolean;
        error?: string;
      } | null;

      if (!response.ok || !result?.success) {
        const errorMessage =
          result?.error ??
          (response.status === 409
            ? t("duplicateEmailError")
            : t("genericError"));
        throw new Error(errorMessage);
      }

      await queryClient.invalidateQueries({
        queryKey: WAITLIST_STATS_QUERY_KEY,
      });

      reset();
      toast.success(t("successMessage"));
    } catch (error) {
      console.error("Error submitting form:", error);
      toast.error(error instanceof Error ? error.message : t("genericError"));
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <>
      {/* Left column: Info */}
      <div className="relative h-[768.555px] w-[40%] shrink-0 overflow-clip">
        <div className="absolute top-6 left-8 flex w-[192px] flex-col gap-16">
          <div className="flex flex-1 flex-col gap-8">
            <div className="flex flex-col gap-4">
              <div className="flex h-[31.998px] items-center gap-[7.997px]">
                <div className="flex items-center gap-2 sm:gap-3">
                  <Image
                    src={ASSETS.icon}
                    alt="trading-platform Icon"
                    width={40}
                    height={40}
                    className="block"
                    unoptimized
                  />
                </div>
              </div>
              <h3 className="h-[71.982px] w-[192px] text-[30px] leading-[36px] tracking-[0.3955px] whitespace-pre-wrap text-zinc-300">
                {t("heading")}
              </h3>
              <p className="w-[192px] text-base leading-[26px] tracking-[-0.3125px] whitespace-pre-wrap text-[#9f9fa9]">
                {t("description")}
              </p>
            </div>

            <div className="flex flex-col gap-[11.99px]">
              {/* Value items */}
              <div className="flex items-start gap-[11.99px]">
                <div className="flex h-[23.99px] w-[23.99px] shrink-0 items-center justify-center rounded-full border-[0.704px] border-white/20 bg-white/10">
                  <Image
                    src={ASSETS.checkIcon}
                    alt="Check"
                    width={14}
                    height={14}
                    unoptimized
                  />
                </div>
                <div className="w-[156.019px]">
                  <p className="h-[19.997px] text-sm leading-5 font-normal text-white">
                    {t("value1.title")}
                  </p>
                  <p className="w-[109px] text-xs leading-4 whitespace-pre-wrap text-[#71717b]">
                    {t("value1.description")}
                  </p>
                </div>
              </div>
              <div className="flex items-start gap-[11.99px]">
                <div className="flex h-[23.99px] w-[23.99px] shrink-0 items-center justify-center rounded-full border-[0.704px] border-white/20 bg-white/10">
                  <Image
                    src={ASSETS.checkIcon}
                    alt="Check"
                    width={14}
                    height={14}
                    unoptimized
                  />
                </div>
                <div className="w-[156.019px]">
                  <p className="h-[19.997px] text-sm leading-5 font-normal text-white">
                    {t("value2.title")}
                  </p>
                  <p className="w-[109px] text-xs leading-4 whitespace-pre-wrap text-[#71717b]">
                    {t("value2.description")}
                  </p>
                </div>
              </div>
              <div className="flex items-start gap-[11.99px]">
                <div className="flex h-[23.99px] w-[23.99px] shrink-0 items-center justify-center rounded-full border-[0.704px] border-white/20 bg-white/10">
                  <Image
                    src={ASSETS.checkIcon}
                    alt="Check"
                    width={14}
                    height={14}
                    unoptimized
                  />
                </div>
                <div className="w-[156.019px]">
                  <p className="h-[19.997px] text-sm leading-5 font-normal text-white">
                    {t("value3.title")}
                  </p>
                  <p className="w-[109px] text-xs leading-4 whitespace-pre-wrap text-[#71717b]">
                    {t("value3.description")}
                  </p>
                </div>
              </div>
            </div>
          </div>

          {/* Spots remaining */}
          <div className="flex flex-col gap-[15.993px]">
            <div className="flex h-[31.998px] items-center justify-between">
              <Label className="h-[19.997px] text-sm leading-5 font-medium tracking-[-0.1504px] text-[#d9d9d9]">
                {t("spotsLabel")}
              </Label>

              <CircularProgress
                value={stats.registeredCount}
                max={stats.maxSpots}
                size={32}
                variant="dark"
              />
            </div>
            <div className="rounded-[10px] border-[0.704px] border-white/10 bg-linear-to-b from-white/10 to-white/5 p-4">
              <div className="flex items-start gap-2.5 px-0 py-[2px]">
                <Image
                  src={ASSETS.aiStarIcon}
                  alt="Info"
                  width={14}
                  height={14}
                  className="shrink-0"
                  unoptimized
                />
                <p className="flex-1 text-xs leading-4 whitespace-pre-wrap text-[#9f9fa9]">
                  {t("spotsInfo")}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Right column: Form */}
      <div className="flex h-[769px] w-[60%] flex-col gap-[23.99px] bg-linear-to-b from-white/10 to-white/5 py-[31.998px] pr-8 pl-[31.998px]">
        <div className="flex shrink-0 flex-col gap-[3.993px]">
          <h2 className="h-[27.994px] text-[20px] leading-7 font-normal tracking-[-0.4492px] text-white">
            {t("formHeading")}
          </h2>
          <p className="text-sm leading-5 font-normal tracking-[-0.1504px] text-[#9f9fa9]">
            {t("formSubtitle")}
          </p>
        </div>

        <div className="flex flex-1 flex-col gap-[10px] overflow-clip">
          <form
            onSubmit={handleSubmit(onSubmit)}
            className="flex h-full flex-col"
          >
            <div className="flex flex-1 flex-col gap-[10px] overflow-y-auto">
              <div className="flex flex-col gap-[36px]">
                {/* Restaurant Name */}
                <div className="flex h-[71.993px] flex-col gap-[7.997px]">
                  <Label className="flex h-[19.997px] items-center gap-2 text-sm leading-5 font-medium tracking-[-0.1504px] text-zinc-300">
                    {t("restaurantName")}
                  </Label>
                  <Input
                    {...register("restaurantName")}
                    placeholder={t("restaurantNamePlaceholder")}
                    className="h-[43.999px] rounded-[8px] border-[0.704px] border-[rgba(255,255,255,0.2)] bg-[rgba(8,8,9,0.25)] px-3 py-1 text-sm leading-normal font-normal text-white placeholder:text-[#71717b] focus-visible:border-[rgba(255,255,255,0.3)]"
                  />
                  {errors.restaurantName && (
                    <p className="text-xs text-red-500">
                      {t("errorRestaurantName")}
                    </p>
                  )}
                </div>

                {/* Number of Locations */}
                <div className="flex h-[63.985px] flex-col gap-[7.997px]">
                  <Label className="flex h-[19.997px] items-center gap-2 text-sm leading-5 font-medium tracking-[-0.1504px] text-zinc-300">
                    {t("numLocations")}
                  </Label>
                  <Select
                    value={numberOfLocationsValue}
                    onValueChange={(value: string) =>
                      setValue("numberOfLocations", value, {
                        shouldValidate: true,
                      })
                    }
                  >
                    <SelectTrigger className="h-[35.991px] w-full rounded-[8px] border-[0.704px] border-[rgba(255,255,255,0.2)] bg-[rgba(8,8,9,0.25)] px-[12.694px] py-[0.704px] text-sm text-white data-placeholder:text-[#717182]">
                      <SelectValue placeholder={t("numLocationsPlaceholder")} />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="1">{t("location1")}</SelectItem>
                      <SelectItem value="2-3">{t("location2")}</SelectItem>
                      <SelectItem value="4-5">{t("location3")}</SelectItem>
                      <SelectItem value="6-10">{t("location4")}</SelectItem>
                      <SelectItem value="11+">{t("location5")}</SelectItem>
                    </SelectContent>
                  </Select>
                  {errors.numberOfLocations && (
                    <p className="text-xs text-red-500">
                      {t("errorNumLocations")}
                    </p>
                  )}
                </div>

                {/* Your Name */}
                <div className="flex h-[71.993px] flex-col gap-[7.997px]">
                  <Label className="flex h-[19.997px] items-center gap-2 text-sm leading-5 font-medium tracking-[-0.1504px] text-zinc-300">
                    {t("yourName")}
                  </Label>
                  <Input
                    {...register("yourName")}
                    placeholder={t("yourNamePlaceholder")}
                    className="h-[43.999px] rounded-[8px] border-[0.704px] border-[rgba(255,255,255,0.2)] bg-[rgba(8,8,9,0.25)] px-3 py-1 text-sm leading-normal font-normal text-white placeholder:text-[#71717b] focus-visible:border-[rgba(255,255,255,0.3)]"
                  />
                  {errors.yourName && (
                    <p className="text-xs text-red-500">{t("errorYourName")}</p>
                  )}
                </div>

                {/* Email and Phone */}
                <div className="flex gap-[16px]">
                  <div className="flex h-[71.993px] flex-1 flex-col gap-[7.997px]">
                    <Label className="flex h-[19.997px] items-center gap-2 text-sm leading-5 font-medium tracking-[-0.1504px] text-zinc-300">
                      {t("email")}
                    </Label>
                    <Input
                      {...register("email")}
                      type="email"
                      placeholder={t("emailPlaceholder")}
                      className="h-[43.999px] rounded-[8px] border-[0.704px] border-[rgba(255,255,255,0.2)] bg-[rgba(8,8,9,0.25)] px-3 py-1 text-sm leading-normal font-normal text-white placeholder:text-[#71717b] focus-visible:border-[rgba(255,255,255,0.3)]"
                    />
                    {errors.email && (
                      <p className="text-xs text-red-500">{t("errorEmail")}</p>
                    )}
                  </div>
                  <div className="flex h-[71.993px] flex-1 flex-col gap-[7.997px]">
                    <Label className="flex h-[19.997px] items-center gap-2 text-sm leading-5 font-medium tracking-[-0.1504px] text-zinc-300">
                      {t("phone")}
                    </Label>
                    <Input
                      {...register("phone")}
                      type="tel"
                      placeholder={t("phonePlaceholder")}
                      className="h-[43.999px] rounded-[8px] border-[0.704px] border-[rgba(255,255,255,0.2)] bg-[rgba(8,8,9,0.25)] px-3 py-1 text-sm leading-normal font-normal text-white placeholder:text-[#71717b] focus-visible:border-[rgba(255,255,255,0.3)]"
                    />
                  </div>
                </div>

                {/* Biggest Challenge */}
                <div className="flex h-[107.983px] flex-col gap-[7.997px]">
                  <Label className="flex h-[19.997px] items-center gap-2 text-sm leading-5 font-medium tracking-[-0.1504px] text-zinc-300">
                    {t("challenge")}
                  </Label>
                  <Textarea
                    {...register("biggestChallenge")}
                    placeholder={t("challengePlaceholder")}
                    className="h-[79.989px] resize-none rounded-[8px] border-[0.704px] border-[rgba(255,255,255,0.2)] bg-[rgba(8,8,9,0.25)] px-3 py-2 text-sm leading-5 font-normal tracking-[-0.1504px] text-white placeholder:text-[#71717b] focus-visible:border-[rgba(255,255,255,0.3)]"
                  />
                </div>
              </div>
            </div>

            {/* Status Message & Submit Button */}
            <div className="shrink-0 border-t-[0.704px] border-[rgba(255,255,255,0.1)] pt-[16.697px]">
              <Button
                type="submit"
                disabled={isSubmitting}
                className="h-[43.999px] w-full rounded-[8px] bg-linear-to-r from-white to-[#f4f4f5] text-sm leading-5 font-medium tracking-[-0.1504px] text-black hover:from-white/90 hover:to-[#f4f4f5]/90 disabled:opacity-50"
              >
                {isSubmitting ? t("submittingButton") : t("submitButton")}
              </Button>
            </div>
          </form>
        </div>
      </div>
    </>
  );
}
