"use client";

import { useTransition } from "react";
import { useParams } from "next/navigation";

import { Button } from "@trading-platform/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from "@trading-platform/ui/dropdown-menu";

import { usePathname, useRouter } from "~/i18n/routing";

const languages = [
  { code: "en", name: "English", flag: "" },
  { code: "tr", name: "Trke", flag: "" },
];

export function LanguageSwitcher() {
  const params = useParams();
  const router = useRouter();
  const pathname = usePathname();
  const [isPending, startTransition] = useTransition();

  const currentLocale = params.locale as string;
  const currentLanguage = languages.find((lang) => lang.code === currentLocale);

  function onSelectChange(nextLocale: string) {
    startTransition(() => {
      router.replace(pathname, { locale: nextLocale });
    });
  }

  return (
    <DropdownMenu>
      <DropdownMenuTrigger asChild>
        <Button
          variant="ghost"
          size="sm"
          className="h-[35.991px] cursor-pointer gap-2 border-[0.704px] border-white/30 bg-[rgba(26,26,26,0.3)] px-3 text-xs font-medium text-white hover:bg-white/20 hover:text-white sm:text-sm"
          disabled={isPending}
        >
          <span>{currentLanguage?.flag}</span>
          <span className="hidden sm:inline">{currentLanguage?.name}</span>
        </Button>
      </DropdownMenuTrigger>
      <DropdownMenuContent align="end" className="border-white/20 bg-[#18181b]">
        {languages.map((language) => (
          <DropdownMenuItem
            key={language.code}
            onClick={() => onSelectChange(language.code)}
            className="cursor-pointer text-white hover:bg-white/10 focus:bg-white/10 focus:text-white"
          >
            <span className="mr-2">{language.flag}</span>
            <span>{language.name}</span>
            {language.code === currentLocale && (
              <span className="ml-auto text-green-500"></span>
            )}
          </DropdownMenuItem>
        ))}
      </DropdownMenuContent>
    </DropdownMenu>
  );
}
