"use client";

import type { Variants } from "framer-motion";
import Image from "next/image";
import { easeOut, motion } from "framer-motion";
import { useTranslations } from "next-intl";

import { cn } from "@trading-platform/ui";
import { Badge } from "@trading-platform/ui/badge";
import { Card, CardTitle } from "@trading-platform/ui/card";

import { ASSETS } from "./constants";

const sectionVariants: Variants = {
  hidden: { opacity: 0, y: 56 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.9, ease: easeOut },
  },
};

const headerVariants: Variants = {
  hidden: { opacity: 0, y: 32 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.75, ease: easeOut, delay: 0.08 },
  },
};

const cardsContainerVariants: Variants = {
  hidden: {},
  visible: {
    transition: {
      staggerChildren: 0.18,
      delayChildren: 0.16,
    },
  },
};

const cardVariants: Variants = {
  hidden: {
    opacity: 0,
    y: 38,
    rotateX: -8,
    scale: 0.95,
    filter: "blur(8px)",
  },
  visible: {
    opacity: 1,
    y: 0,
    rotateX: 0,
    scale: 1,
    filter: "blur(0px)",
    transition: { duration: 0.82, ease: easeOut },
  },
};

const csvCardVariants: Variants = {
  hidden: { opacity: 0, y: 42, scale: 0.96 },
  visible: {
    opacity: 1,
    y: 0,
    scale: 1,
    transition: { duration: 0.85, ease: easeOut, delay: 0.22 },
  },
};

const csvFeaturesVariants: Variants = {
  hidden: {},
  visible: {
    transition: { staggerChildren: 0.08, delayChildren: 0.12 },
  },
};

const csvFeatureItemVariants: Variants = {
  hidden: { opacity: 0, y: 10 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.45, ease: easeOut },
  },
};

const MotionCard = motion(Card);

export function POSIntegrationSection() {
  const t = useTranslations("posIntegration");
  const posSystems = [
    {
      name: t("sambaPOS.name"),
      desc: t("sambaPOS.description"),
      comingSoon: true,
      badge: t("sambaPOS.badge"),
    },
    {
      name: t("menulux.name"),
      desc: t("menulux.description"),
      comingSoon: true,
      badge: t("sambaPOS.badge"),
    },
    {
      name: t("anyPOS.name"),
      desc: t("anyPOS.description"),
    },
  ];

  const csvFeatures = [
    { icon: ASSETS.checkIcon, text: t("csvCard.feature1") },
    { icon: ASSETS.checkIcon, text: t("csvCard.feature2") },
    { icon: ASSETS.checkIcon, text: t("csvCard.feature3") },
    {
      icon: ASSETS.checkIcon,
      text: t("csvCard.feature4"),
    },
  ];

  return (
    <motion.section
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, amount: 0.28 }}
      variants={sectionVariants}
      className="mx-auto w-full max-w-[1440px] py-8 sm:py-12 md:py-16"
    >
      <motion.div
        variants={headerVariants}
        className="mx-auto flex w-full max-w-[1280px] flex-col items-center gap-8 px-4 text-center sm:gap-12 sm:px-6 md:gap-16"
      >
        <div className="flex w-full flex-col items-center gap-4 sm:gap-6">
          <h2 className="w-full bg-linear-to-b from-white to-[#d4d4d8] bg-clip-text px-4 text-3xl leading-tight font-normal tracking-[0.2637px] text-transparent sm:text-4xl sm:leading-[1.2] md:text-5xl md:leading-[60px] lg:text-[60px]">
            {t("heading")}
          </h2>
          <motion.div
            variants={{ hidden: { scaleX: 0 }, visible: { scaleX: 1 } }}
            transition={{ duration: 0.6, ease: easeOut }}
            className="h-[0.99px] w-[79.989px] origin-center bg-linear-to-r from-transparent via-white/30 to-transparent"
          />
          <p className="px-4 text-sm leading-[29.25px] tracking-[-0.4395px] text-[#9f9fa9] sm:text-base md:text-[18px]">
            {t("subtitle")}
          </p>
        </div>

        <motion.div
          variants={cardsContainerVariants}
          className="flex w-full flex-col flex-wrap items-center justify-center gap-4 sm:flex-row sm:gap-6"
        >
          {posSystems.map((pos) => (
            <MotionCard
              key={pos.name}
              variants={cardVariants}
              whileHover={{
                y: 0,
                scale: 0.98,
                borderColor: "rgba(255,255,255,0.4)",
                boxShadow:
                  "0 0 0 1px rgba(148,163,184,0.18), 0 0 18px rgba(125,211,252,0.18)",
              }}
              whileTap={{ scale: 0.98 }}
              transition={{ type: "tween", duration: 0.2 }}
              className={cn(
                pos.comingSoon
                  ? "bg-linear-to-b from-[rgba(24,24,27,0.5)] to-[rgba(0,0,0,0.5)]"
                  : "bg-linear-to-t from-white/5 to-white/10",
                "flex h-auto w-full max-w-[282.669px] min-w-[240px] flex-col items-center justify-center border-[0.704px] border-white/20 p-4 sm:h-[139px] sm:w-[calc(33.333%-1rem)] sm:p-6",
              )}
              style={{
                transformPerspective: 1200,
                borderColor: "rgba(255,255,255,0.2)",
                boxShadow: "0 0 0 0 rgba(0,0,0,0)",
              }}
            >
              <CardTitle className="text-sm font-normal tracking-[-0.3125px] text-white sm:text-base">
                {pos.name}
              </CardTitle>
              <div className="flex items-center gap-2">
                {pos.comingSoon ? (
                  <Badge
                    variant="outline"
                    className="mt-1 flex border-[0.704px] border-white/30 bg-linear-to-r from-white/10 via-white/5 to-white/10 text-xs font-normal tracking-[0.1996px] text-[#9f9fa9]"
                  >
                    {pos.badge}
                  </Badge>
                ) : (
                  <p className="mt-1 text-xs leading-[20px] tracking-[-0.1504px] text-[#71717b] sm:text-sm">
                    {pos.desc}
                  </p>
                )}
              </div>
            </MotionCard>
          ))}
        </motion.div>

        <motion.div
          variants={csvCardVariants}
          className="w-full max-w-[896px]"
          style={{ transformPerspective: 1300 }}
        >
          <Card className="border-[0.704px] border-white/30 bg-linear-to-b from-white/10 to-white/5 p-6 sm:p-8">
            <CardTitle className="mb-3 text-center text-xl font-normal tracking-[0.0703px] text-white sm:text-2xl">
              {t("csvCard.title")}
            </CardTitle>
            <p className="mb-4 text-center text-sm leading-[24px] tracking-[-0.3125px] text-[#9f9fa9] sm:mb-6 sm:text-base">
              {t("csvCard.subtitle")}
            </p>
            <motion.div
              variants={csvFeaturesVariants}
              initial="hidden"
              animate="visible"
              className="flex flex-wrap justify-center gap-3 sm:gap-4"
            >
              {csvFeatures.map((feature, idx) => (
                <motion.div
                  key={idx}
                  variants={csvFeatureItemVariants}
                  className="flex items-center gap-2 text-sm leading-[24px] tracking-[-0.3125px] text-[#9f9fa9] sm:text-base"
                >
                  <Image
                    src={feature.icon}
                    alt=""
                    width={16}
                    height={16}
                    className="shrink-0"
                    unoptimized
                  />
                  <span>{feature.text}</span>
                </motion.div>
              ))}
            </motion.div>
          </Card>
        </motion.div>

        <motion.p
          variants={{
            hidden: { opacity: 0, y: 24 },
            visible: { opacity: 1, y: 0 },
          }}
          transition={{ duration: 0.7, ease: easeOut, delay: 0.18 }}
          className="w-full max-w-4xl bg-linear-to-r from-white to-[#d4d4d8] bg-clip-text px-4 text-xl font-normal tracking-[0.0703px] text-transparent sm:text-2xl"
        >
          {t("closingStatement")}
        </motion.p>
      </motion.div>
    </motion.section>
  );
}
