"use client";

import type { Variants } from "framer-motion";
import Image from "next/image";
import { easeOut, motion } from "framer-motion";
import { useTranslations } from "next-intl";

import { Card, CardContent, CardHeader, CardTitle } from "@trading-platform/ui/card";

import { ASSETS } from "./constants";

const sectionVariants: Variants = {
  hidden: { opacity: 0, y: 56 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.9, ease: easeOut },
  },
};

const headerVariants: Variants = {
  hidden: { opacity: 0, y: 32 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.8, ease: easeOut, delay: 0.1 },
  },
};

const gridVariants: Variants = {
  hidden: {},
  visible: {
    transition: {
      staggerChildren: 0.18,
      delayChildren: 0.2,
    },
  },
};

const cardVariants: Variants = {
  hidden: {
    opacity: 0,
    y: 42,
    scale: 0.96,
    rotateX: -8,
    filter: "blur(10px)",
  },
  visible: {
    opacity: 1,
    y: 0,
    scale: 1,
    rotateX: 0,
    filter: "blur(0px)",
    transition: { duration: 0.85, ease: easeOut },
  },
};

const listItemVariants: Variants = {
  hidden: { opacity: 0, x: -12 },
  visible: {
    opacity: 1,
    x: 0,
    transition: { duration: 0.4, ease: easeOut },
  },
};

const MotionCard = motion(Card);

export function FeaturesSection() {
  const t = useTranslations("features");
  const categories = [
    {
      icon: ASSETS.operationsIcon,
      title: t("operations.title"),
      items: [
        t("operations.item1"),
        t("operations.item2"),
        t("operations.item3"),
        t("operations.item4"),
        t("operations.item5"),
      ],
    },
    {
      icon: ASSETS.usersIcon,
      title: t("customer.title"),
      items: [
        t("customer.item1"),
        t("customer.item2"),
        t("customer.item3"),
        t("customer.item4"),
      ],
    },
    {
      icon: ASSETS.aiStarIcon,
      title: t("intelligence.title"),
      items: [
        t("intelligence.item1"),
        t("intelligence.item2"),
        t("intelligence.item3"),
        t("intelligence.item4"),
        t("intelligence.item5"),
      ],
    },
    {
      icon: ASSETS.managementIcon,
      title: t("management.title"),
      items: [
        t("management.item1"),
        t("management.item2"),
        t("management.item3"),
        t("management.item4"),
      ],
    },
  ];

  return (
    <motion.section
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, amount: 0.25 }}
      variants={sectionVariants}
      className="mx-auto w-full max-w-[1440px] py-8 sm:py-12 md:py-16"
    >
      <motion.div
        variants={headerVariants}
        className="mx-auto flex w-full max-w-[1280px] flex-col items-center gap-8 px-4 sm:gap-12 sm:px-6 md:gap-16"
      >
        <div className="flex w-full flex-col items-center gap-4 text-center sm:gap-6">
          <h2 className="w-full bg-linear-to-b from-white to-[#d4d4d8] bg-clip-text px-4 text-3xl leading-tight font-normal tracking-[0.2637px] text-transparent sm:text-4xl sm:leading-[1.2] md:text-5xl md:leading-[60px] lg:text-[60px]">
            {t("heading")}
          </h2>
          <motion.div
            variants={{ hidden: { scaleX: 0 }, visible: { scaleX: 1 } }}
            transition={{ duration: 0.6, ease: easeOut }}
            className="h-[0.99px] w-[79.989px] origin-center bg-linear-to-r from-transparent via-white/30 to-transparent"
          />
          <p className="px-4 text-sm leading-[28px] tracking-[-0.4395px] text-white sm:text-base md:text-[18px]">
            {t("subtitle")}
          </p>
        </div>

        {/* Feature Grid */}
        <motion.div
          variants={gridVariants}
          className="grid w-full grid-cols-1 gap-4 px-0 sm:gap-6 sm:px-4 md:grid-cols-2 md:gap-8 md:px-8 lg:px-16"
        >
          {categories.map((category) => (
            <MotionCard
              key={category.title}
              variants={cardVariants}
              whileHover={{
                y: -12,
                scale: 1.02,
                boxShadow: "0px 28px 70px rgba(0,0,0,0.28)",
              }}
              whileTap={{ scale: 0.995 }}
              transition={{ type: "spring", stiffness: 240, damping: 22 }}
              className="border-[0.704px] border-white/20 bg-linear-to-b from-[rgba(24,24,27,0.5)] to-[rgba(0,0,0,0.5)] p-6 sm:p-8"
              style={{ transformPerspective: 1300 }}
            >
              <CardHeader>
                <div className="mb-4 flex items-center gap-3">
                  <Image
                    src={category.icon}
                    alt=""
                    width={24}
                    height={24}
                    className="shrink-0"
                    unoptimized
                  />
                  <CardTitle className="text-xl font-normal tracking-[0.0703px] text-white sm:text-2xl">
                    {category.title}
                  </CardTitle>
                </div>
              </CardHeader>
              <CardContent>
                <motion.ul
                  initial="hidden"
                  animate="visible"
                  variants={{
                    hidden: {},
                    visible: {
                      transition: { staggerChildren: 0.08, delayChildren: 0.1 },
                    },
                  }}
                  className="flex flex-col gap-3"
                >
                  {category.items.map((item, idx) => (
                    <motion.li
                      key={idx}
                      variants={listItemVariants}
                      className="flex items-start gap-3 text-sm leading-[24px] tracking-[-0.3125px] text-[#9f9fa9] sm:text-base"
                    >
                      <Image
                        src={ASSETS.checkIcon}
                        alt=""
                        width={20}
                        height={20}
                        className="mt-0.5 shrink-0"
                        unoptimized
                      />
                      <span className="flex-1">{item}</span>
                    </motion.li>
                  ))}
                </motion.ul>
              </CardContent>
            </MotionCard>
          ))}
        </motion.div>
      </motion.div>
    </motion.section>
  );
}
