"use client";

import Image from "next/image";
import { motion } from "framer-motion";
import { useTranslations } from "next-intl";

import { Badge } from "@trading-platform/ui/badge";
import { Card, CardTitle } from "@trading-platform/ui/card";

import { ASSETS } from "./constants";

const cardVariants = {
  hidden: { opacity: 0, y: 32 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.6 },
  },
};

const cardHoverVariants = {
  rest: {
    y: 0,
    scale: 1,
    boxShadow: "0px 0px 0px rgba(15, 23, 42, 0)",
    transition: { duration: 0.4 },
  },
  hover: {
    y: -6,
    scale: 1.01,
    boxShadow: "0px 20px 60px rgba(15, 23, 42, 0.3)",
    transition: { duration: 0.4 },
  },
};

const iconVariants = {
  rest: { scale: 1, transition: { duration: 0.3 } },
  hover: { scale: 1.12, transition: { duration: 0.3 } },
};

const exampleVariants = {
  rest: {
    scale: 1,
    y: 0,
    transition: { duration: 0.3 },
  },
  hover: {
    scale: 1.04,
    y: -2,
    transition: { duration: 0.3 },
  },
};

export function SolutionSection() {
  const t = useTranslations("solution");
  const solutions = [
    {
      icon: ASSETS.checkIcon,
      title: t("card1.title"),
      description: t("card1.description"),
    },
    {
      icon: ASSETS.icon4,
      title: t("card2.title"),
      description: t("card2.description"),
    },
    {
      icon: ASSETS.aiStarIcon,
      title: t("card3.title"),
      description: t("card3.description"),
      example: t("card3.example"),
    },
    {
      icon: ASSETS.icon5,
      title: t("card4.title"),
      description: t("card4.description"),
    },
    {
      icon: ASSETS.icon6,
      title: t("card5.title"),
      description: t("card5.description"),
    },
  ];

  return (
    <section className="mx-auto w-full max-w-[1440px] py-8 sm:py-12 md:py-16">
      <div className="mx-auto flex w-full max-w-[1280px] flex-col items-center gap-8 px-4 sm:gap-12 sm:px-6 md:gap-[64px]">
        {/* Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 24 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.7, ease: "easeOut" }}
          viewport={{ once: true, amount: 0.4 }}
          className="flex w-full flex-col items-center gap-4 text-center sm:gap-6"
        >
          <Badge
            variant="outline"
            className="flex border-[0.704px] border-white/20 bg-white/10 px-[12.694px] py-[0.704px] text-xs font-normal tracking-[0.3px] text-[#9f9fa9]"
          >
            <Image
              src={ASSETS.icon7}
              alt=""
              width={12}
              height={12}
              className="mr-[7.997px] shrink-0"
              unoptimized
            />
            {t("badge")}
          </Badge>
          <h2 className="w-full max-w-[1152px] bg-linear-to-b from-white to-[#d4d4d8] bg-clip-text px-4 text-center text-3xl leading-tight font-normal tracking-[0.2637px] text-transparent sm:text-4xl sm:leading-[1.2] md:text-5xl md:leading-[60px] lg:text-[60px]">
            {t("heading")}
          </h2>
          <div className="h-[0.99px] w-[79.989px] bg-linear-to-r from-transparent via-white/30 to-transparent" />
          <p className="w-full max-w-[768px] px-4 text-sm leading-relaxed tracking-[-0.4395px] text-[#9f9fa9] sm:px-0 sm:text-base sm:leading-[29.25px] md:text-[18px]">
            {t("description")}
          </p>
        </motion.div>

        {/* Solution Cards */}
        <div className="flex w-full flex-col gap-4 px-0 sm:gap-6 sm:px-4 md:px-8 lg:px-16">
          {solutions.map((solution, index) => (
            <motion.div
              key={solution.title}
              variants={cardVariants}
              initial="hidden"
              whileInView="visible"
              viewport={{ once: true, amount: 0.3 }}
              transition={{ delay: index * 0.08 }}
            >
              <motion.div
                variants={cardHoverVariants}
                initial="rest"
                animate="rest"
                whileHover="hover"
                whileTap={{ scale: 0.995 }}
                className="rounded-[20px]"
                style={{ boxShadow: "0px 0px 0px rgba(15, 23, 42, 0)" }}
              >
                <Card className="group border-[0.704px] border-white/20 bg-linear-to-b from-[rgba(24,24,27,0.55)] to-[rgba(0,0,0,0.45)] p-6 transition-colors duration-300 sm:p-8">
                  <div className="flex flex-col gap-4 sm:flex-row sm:gap-6">
                    <motion.div
                      variants={iconVariants}
                      className="flex h-12 w-12 shrink-0 items-center justify-center rounded-xl border-[0.704px] border-white/30 bg-linear-to-br from-white/20 to-white/10 sm:h-14 sm:w-14"
                    >
                      <Image
                        src={solution.icon}
                        alt=""
                        width={24}
                        height={24}
                        unoptimized
                      />
                    </motion.div>
                    <div className="flex flex-1 items-center justify-between gap-4">
                      <div className="text-left">
                        <CardTitle className="text-xl font-normal tracking-[0.0703px] text-white sm:text-2xl">
                          {solution.title}
                        </CardTitle>
                        <p className="text-sm leading-[26px] tracking-[-0.3125px] text-[#9f9fa9] sm:text-base">
                          {solution.description}
                        </p>
                      </div>
                      {solution.example && (
                        <motion.div
                          variants={exampleVariants}
                          className="rounded-[10px] border-[0.704px] border-white/20 bg-white/5 p-3 text-sm leading-[24px] tracking-[-0.3125px] text-white italic sm:p-4 sm:text-base"
                        >
                          {solution.example}
                        </motion.div>
                      )}
                    </div>
                  </div>
                </Card>
              </motion.div>
            </motion.div>
          ))}
        </div>

        <motion.p
          initial={{ opacity: 0, y: 16 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.7 }}
          viewport={{ once: true, amount: 0.4 }}
          className="w-full max-w-4xl bg-linear-to-r from-white to-[#d4d4d8] bg-clip-text px-4 text-center text-xl font-normal tracking-[0.0703px] text-transparent sm:text-2xl"
        >
          {t("closingStatement")}
        </motion.p>
      </div>
    </section>
  );
}
