"use client";

import type { Variants } from "framer-motion";
import { easeOut, motion } from "framer-motion";
import Image from "next/image";
import { useTranslations } from "next-intl";

import { Badge } from "@trading-platform/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@trading-platform/ui/card";

import { ASSETS } from "./constants";

const sectionVariants: Variants = {
  hidden: { opacity: 0, y: 56 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.9, ease: easeOut },
  },
};

const headerVariants: Variants = {
  hidden: { opacity: 0, y: 32 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.8, ease: easeOut, delay: 0.08 },
  },
};

const cardsContainerVariants: Variants = {
  hidden: {},
  visible: {
    transition: {
      staggerChildren: 0.22,
      delayChildren: 0.18,
    },
  },
};

const cardVariants: Variants = {
  hidden: {
    opacity: 0,
    y: 48,
    rotateX: -10,
    scale: 0.94,
    filter: "blur(12px)",
  },
  visible: {
    opacity: 1,
    y: 0,
    rotateX: 0,
    scale: 1,
    filter: "blur(0px)",
    transition: { duration: 0.9, ease: easeOut },
  },
};

const closingVariants: Variants = {
  hidden: { opacity: 0, y: 24 },
  visible: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.8, ease: easeOut, delay: 0.32 },
  },
};

const MotionCard = motion(Card);

export function ProblemSection() {
  const t = useTranslations("problem");
  const problems = [
    {
      icon: ASSETS.icon6,
      title: t("insight.title"),
      items: [t("insight.item1"), t("insight.item2"), t("insight.item3")],
    },

    {
      icon: ASSETS.icon5,
      title: t("time.title"),
      items: [t("time.item1"), t("time.item2"), t("time.item3")],
    },
    {
      icon: ASSETS.icon4,
      title: t("money.title"),
      items: [t("money.item1"), t("money.item2"), t("money.item3")],
    },
  ];

  return (
    <motion.section
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, amount: 0.28 }}
      variants={sectionVariants}
      className="mx-auto w-full max-w-[1440px] py-8 sm:py-12 md:py-16"
    >
      <motion.div
        variants={headerVariants}
        className="mx-auto flex w-full max-w-[1280px] flex-col items-center gap-8 px-4 sm:gap-12 sm:px-6 md:gap-[64px]"
      >
        {/* Section Header */}
        <div className="flex w-full flex-col items-center gap-4 sm:gap-5">
          <Badge
            variant="outline"
            className="flex border-[0.704px] border-white/20 bg-white/10 px-[12.694px] py-[0.704px] text-xs font-normal tracking-[0.3px] text-[#9f9fa9]"
          >
            <Image
              src={ASSETS.icon3}
              alt=""
              width={12}
              height={12}
              className="mr-[7.997px] shrink-0"
              unoptimized
            />
            {t("badge")}
          </Badge>
          <h2 className="w-full max-w-[1152px] bg-gradient-to-b from-white to-[#d4d4d8] bg-clip-text px-4 text-center text-3xl leading-tight font-normal tracking-[0.2637px] text-transparent sm:text-4xl sm:leading-[1.2] md:text-5xl md:leading-[60px] lg:text-[60px]">
            {t("heading")}
          </h2>
        </div>

        {/* Problem Cards */}
        <motion.div
          variants={cardsContainerVariants}
          className="flex w-full flex-col flex-wrap justify-center gap-4 sm:flex-row sm:gap-6 md:gap-8"
        >
          {problems.map((problem) => (
            <MotionCard
              key={problem.title}
              variants={cardVariants}
              whileHover={{
                y: -10,
                scale: 1.02,
                boxShadow: "0px 25px 60px rgba(0,0,0,0.25)",
              }}
              whileTap={{ scale: 0.99 }}
              transition={{ type: "spring", stiffness: 220, damping: 26 }}
              className="w-full max-w-full min-w-[280px] flex-1 border-[0.704px] border-white/20 bg-gradient-to-b from-[rgba(24,24,27,0.5)] to-[rgba(0,0,0,0.5)] p-6 sm:w-full sm:p-8 md:w-[calc(50%-1rem)] lg:w-[362.669px]"
              style={{ transformPerspective: 1200 }}
            >
              <CardHeader>
                <div className="mb-8 flex h-12 w-12 items-center justify-center overflow-hidden rounded-2xl border border-white/30 bg-gradient-to-br from-white/20 to-white/15 sm:mb-12 sm:h-14 sm:w-14">
                  <Image
                    src={problem.icon}
                    alt=""
                    width={28}
                    height={28}
                    unoptimized
                  />
                </div>
                <CardTitle className="text-xl font-normal tracking-[0.0703px] text-white sm:text-2xl">
                  {problem.title}
                </CardTitle>
              </CardHeader>
              <CardContent>
                <ul className="flex flex-col gap-3">
                  {problem.items.map((item, idx) => (
                    <li
                      key={idx}
                      className="flex items-start gap-3 text-sm leading-[24px] tracking-[-0.3125px] text-[#9f9fa9] sm:gap-[13.99px] sm:text-base"
                    >
                      <span className="mt-2 h-[5.995px] w-[5.995px] shrink-0 rounded-full bg-white" />
                      <span className="flex-1">{item}</span>
                    </li>
                  ))}
                </ul>
              </CardContent>
            </MotionCard>
          ))}
        </motion.div>

        <motion.p
          variants={closingVariants}
          className="w-full max-w-4xl bg-gradient-to-r from-white to-[#d4d4d8] bg-clip-text px-4 text-center text-xl font-normal tracking-[0.0703px] text-transparent sm:text-2xl"
        >
          {t("closingStatement")}
        </motion.p>
      </motion.div>
    </motion.section>
  );
}
